import { Component, Prop, State, Event, EventEmitter, Watch, h } from '@stencil/core';

export interface DropdownMenuItem {
  id: string;
  label?: string;
  icon?: string;
  shortcut?: string;
  disabled?: boolean;
  divider?: boolean;
}

export interface DropdownMenuHeader {
  title?: string;
  avatarGroup?: boolean;
  avatarSrc?: string;
  name?: string;
  email?: string;
}

@Component({
  tag: 'bh-dropdown-menu',
  styleUrl: 'bh-dropdown-menu.css',
  shadow: true,
})
export class BhDropdownMenu {
  /**
   * Header configuration (JSON string or object)
   */
  @Prop() header?: DropdownMenuHeader | string;

  /**
   * Menu items array (JSON string or array)
   */
  @Prop() menuItems: DropdownMenuItem[] | string = [];

  /**
   * Whether the menu is visible
   */
  @Prop() visible: boolean = true;

  /**
   * Whether to show icons in menu items
   */
  @Prop() showIcons: boolean = true;

  /**
   * Whether the menu should be scrollable
   */
  @Prop() scrollable: boolean = false;

  @State() isClosing: boolean = false;
  @State() shouldRender: boolean = true;

  /**
   * Event emitted when a menu item is clicked
   */
  @Event() bhItemClick!: EventEmitter<DropdownMenuItem>;

  private get parsedHeader(): DropdownMenuHeader | undefined {
    if (!this.header) return undefined;
    if (typeof this.header === 'string') {
      try {
        return JSON.parse(this.header);
      } catch {
        return undefined;
      }
    }
    return this.header;
  }

  private get parsedMenuItems(): DropdownMenuItem[] {
    if (typeof this.menuItems === 'string') {
      try {
        return JSON.parse(this.menuItems);
      } catch {
        return [];
      }
    }
    return this.menuItems || [];
  }

  @Watch('visible')
  watchVisible(newValue: boolean) {
    if (newValue) {
      this.shouldRender = true;
      this.isClosing = false;
    } else {
      this.isClosing = true;
      setTimeout(() => {
        this.shouldRender = false;
        this.isClosing = false;
      }, 150);
    }
  }

  componentWillLoad() {
    this.shouldRender = this.visible;
  }

  private handleItemClick(item: DropdownMenuItem) {
    if (!item.disabled && !item.divider) {
      this.bhItemClick.emit(item);
    }
  }

  private getMenuItemClasses(item: DropdownMenuItem) {
    return {
      'dropdown-item': true,
      'dropdown-item-disabled': !!item.disabled,
    };
  }

  render() {
    if (!this.visible) {
      return null;
    }

    const header = this.parsedHeader;
    const items = this.parsedMenuItems;

    const dropdownClasses = {
      'dropdown-menu': true,
      'dropdown-menu-scrollable': this.scrollable,
      'animate-dropdown-enter': true,
      'animate-dropdown-exit': false,
    };

    const headerClasses = {
      'dropdown-header': true,
      'dropdown-header-avatar': !!header?.avatarGroup,
    };

    return (
      <div class={dropdownClasses}>
        {/* Header Section */}
        {header && (
          <div class={headerClasses}>
            {/* Avatar Group Header */}
            {header.avatarGroup && (
              <div class="avatar-group">
                <bh-avatar
                  size="md"
                  type="image"
                  status="online"
                  imageSrc={header.avatarSrc || ''}
                  alt={header.name || 'Avatar'}
                />
                <div class="avatar-text">
                  <div class="avatar-name">{header.name}</div>
                  <div class="avatar-email">{header.email}</div>
                </div>
              </div>
            )}
            {/* Simple Text Header */}
            {!header.avatarGroup && header.title && (
              <div class="header-title">{header.title}</div>
            )}
          </div>
        )}

        {/* Menu Items Section */}
        <div class="menu-items">
          {items.map((item) => (
            <div key={item.id}>
              {/* Divider */}
              {item.divider && <div class="menu-divider" />}

              {/* Menu Item */}
              {!item.divider && (
                <div
                  class={this.getMenuItemClasses(item)}
                  onClick={() => this.handleItemClick(item)}
                >
                  <div class="item-content">
                    <div class="item-main">
                      {item.icon && this.showIcons && (
                        <span class="material-symbols-outlined item-icon">
                          {item.icon}
                        </span>
                      )}
                      <span class="item-text" title={item.label}>
                        {item.label}
                      </span>
                    </div>
                    {item.shortcut && (
                      <span class="item-shortcut">{item.shortcut}</span>
                    )}
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      </div>
    );
  }
}
